body, html, .App {
  margin: 0;
  padding: 0;
  height: 100vh;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

body {
  background-color: lightgray;
  font-family: 'Roboto', sans-serif;
}

.paper {
  text-align: center;
  width: 60%;
  overflow: auto !important;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.margin-bottom {
  margin-bottom: 10px;
}

.heading {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.typography {
  padding-bottom: 10px;
}

.result {
  padding: 8px 24px;
  background-color: aquamarine;
}

.paddingTop0 {
  padding-top: 0;
}

.icon {
  padding: 16px 0;
}

@media only screen and (max-width: 600px) {
  .paper {
    width: 100%;
    height: 100%;
  }

  .icon {
    padding: 0;
  }
}


